<template>
	<div id="userFeedback">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" style="color: #000 !important;" @click="$router.back(-1)"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">我要反馈</div>
        <div class="van-nav-bar__right" @click="browse">
          反馈
        </div>
      </div>
    </div>
    <div class="body_box">
      <div class="background_color_ffffff" style="padding: 10px">
        <textarea v-model="content" style="width: 98%;
    height: 60px;
    line-height: 16px;
    border: 0px;
    font-size: 14px;" placeholder="请描述您要反馈的问题,您提供的有效截图有助于我们的沟通和解决问题"></textarea>
        <!--<img src="static/img/img_sczp_fk.png" width="30%"/>-->
        <div class="aaa">
          <img :src="image" width="30%" v-for="(image,index) in imgList.split(',')"/>
          <img src="static/img/img_sczp_wdfk1.png" width="30%" @click="uploadImg"/>
        </div>
      </div>
      <div class="login_bar background_color_ffffff font_12" style="margin: 5px 0;">
        <span style="display: inline-block;
    width: 66px;">联系人</span>
        <input class="font_14 login_input" type="text" placeholder="请输入你的用户名" v-model="username"/>
      </div>
      <div class="login_bar background_color_ffffff font_12" style="margin: 5px 0;">
        <span style="display: inline-block;
    width: 66px;">联系方式</span>
        <input class="font_14 login_input1" type="text" placeholder="QQ,邮箱或者联系电话" v-model="lianxi"/>
      </div>
      <div class="login_bar background_color_ffffff font_12" style="margin: 5px 0;display: flex;align-items: center ">
        <span class="color_c10804" style="display: inline-block;
    width: 66px;">客服电话</span>
        <span class="font_20 color_c10804">0516-88995851</span>
      </div>
      <!--<div class="background_color_ffffff feedback_bottom padding_top_bottom_5">-->
        <!--<div style="width: 50%;float: left;text-align: right;">-->
          <!--<img src="static/img/img_kfqq_wdfk1.png" width="50px" style="margin-right: 20px;"/>-->
        <!--</div>-->
        <!--<div style="width: 50%;float: left;line-height: 25px">-->
          <!--<div class="color_c10804">客服QQ</div>-->
          <!--<div class="font_20 color_c10804">564654654</div>-->
        <!--</div>-->
      <!--</div>-->
    </div>
	</div>
</template>

<script>
	export default {
		name: 'userFeedback',
		data() {
			return {
        content:'',
        img:'',
        username:'',
        lianxi:'',
        userId:localStorage.getItem('userId'),
        imgList:'',
			}
		},
		methods: {
      uploadImg(){
        let $this = this;
        dsBridge.call('uploadImg');
        dsBridge.register('addImgCallback', function(rpdata) {
          $this.imgList = rpdata;
        })
      },
      browse(){
        let $this = this;
        let obj = {
          userid: $this.userId,
          lianxi: $this.lianxi,
          content: $this.content,
          username: $this.username,
          img: $this.imgList,
        };
        let qs = require('qs');
        $this.$ajax.post("browse/addbrowse", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.$toast({
              message: '反馈成功!',
              position: "bottom"
            });
            $this.$router.back(-1)
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
		}
	}
</script>
<style scoped>
  .aaa{

  }
  .feedback_bottom {
    position: absolute;
    width: 100%;
    bottom: 0;
  }
  .login_input1{
    border: 0;
  }
  .login_input{
    border: 0;
  }
  .login_bar{
    padding: 10px 15px;
  }
  .body_box{
    margin-top: 50px;
  }
</style>



